.move-list {
    margin-top: 5px;
    background-color: #e5f2fe;
    min-width: 300px;
    border-radius: 10px;
    padding: 15px;
    height: fit-content;
    max-height: 362px;
    padding: 10px 20px;
    overflow-y: auto;
    >*:not(:last-child){
    border-bottom: 1px solid #2a2a2a70;

    }
    h2{
        border-bottom: 1px solid #2a2a2a70;
        padding-bottom: 5px;
        line-height: 1.3;
        margin-bottom: 5px;
    }
}